import React, { Component, PropTypes } from 'react';
import { Motion, spring, presets } from 'react-motion';
import c from 'classnames';
import './ProjectPage.css';

const wobbly = (val) => spring(val, presets.wobbly)

export default class ProjectPage extends Component {
  static propTypes = {};
  constructor(props) {
    super(props);
    this.state = {
      atProject: 1,
      motion: {
        project1: {
          rotateZ: 0,
          opacity: 1
        },
        caption1: {
          x: 0,
          opacity: 1
        },
        project2: {
          rotateZ: 45,
          opacity: 0
        },
        caption2: {
          x: 100,
          opacity: 0
        }
      }
    }
  }

  switchProject = (next) => {
    const {atProject} = this.state;
    if (next) {
      (atProject < 2) && this.setState({
        atProject: atProject + 1,
        motion: {
          project2: {
            rotateZ: wobbly(0),
            opacity: wobbly(1)
          },
          caption2: {
            x: spring(0),
            opacity: spring(1)
          },
          project1: {
            rotateZ: wobbly(-45),
            opacity: wobbly(0)
          },
          caption1: {
            x: spring(-100),
            opacity: spring(0)
          }
        }
      });
    } else {
      (atProject > 1) && this.setState({
        atProject: atProject - 1,
        motion: {
          project1: {
            rotateZ: wobbly(0),
            opacity: wobbly(1)
          },
          caption1: {
            opacity: spring(1),
            x: spring(0)
          },
          project2: {
            rotateZ: wobbly(45),
            opacity: wobbly(0)
          },
          caption2: {
            opacity: spring(0),
            x: spring(100)
          }
        }
      })
    }
  };

  render() {
    const {
      atProject,
      motion: {
        project1,
        project2,
        caption1,
        caption2
      }
    } = this.state;

    return (
      <div className="page projectPage">
        <h1 className="page__title">PROJECTS</h1>

        <div className="projects">
          <div className="project project1">
            <Motion style={project1}>
              {({ rotateZ, x, y, opacity }) =>
                <div className="projectThumbnail" style={{
                  opacity,
                  transform: `rotateZ(${rotateZ}deg)`
                }}>
                  <img src={require('../../assets/frodev_thumbnail.png')} alt=""/>
                </div>
              }
            </Motion>
            <Motion style={caption1}>
              {({ opacity, x }) =>
                <div className="projectCaption" style={{
                  opacity,
                  transform: `translateX(${x}px)`
                }}>
                  <p className="name lead">Frodev</p>
                  <p className="description">Bookmark Manager</p>
                </div>
              }
            </Motion>
          </div>
          <div className="project project2">
            <Motion style={project2}>
              {({ rotateZ, x, y, opacity }) =>
                <div className="projectThumbnail" style={{
                  opacity,
                  transform: `rotateZ(${rotateZ}deg)`
                }}>
                  <img src={require('../../assets/tilt-hover.png')} alt=""/>
                </div>
              }
            </Motion>
            <Motion style={caption2}>
              {({ opacity, x }) =>
                <div className="projectCaption" style={{
                      opacity,
                    transform: `translateX(${x}px)`
                  }}>
                  <p className="name lead">Tilt Hover</p>
                  <p className="description">A React Component implementing hover effect inspired by Codrop</p>
                </div>
              }
            </Motion>
          </div>
        </div>

        <div className="sliderControl">
          <div>
            <a className={c({ disabled: atProject === 1 })} onClick={() => this.switchProject()}>
              <i className="fa fa-arrow-left"></i>
              <i className="fa fa-arrow-left"></i>
            </a>
          </div>
          <div>
            <a>
              <i className="fa fa-search"></i>
            </a>
          </div>
          <div>

            <a className={c({ disabled: atProject === 2 })} onClick={() => this.switchProject(true)}>
              <i className="fa fa-arrow-right"></i>
              <i className="fa fa-arrow-right"></i>
            </a>
          </div>
        </div>
      </div>
    );
  }
}



