import React, { Component, PropTypes } from 'react';
import { Motion, spring, presets } from 'react-motion';
import c from 'classnames';
import setStateWithTimeline from '../../helpers/setStateWithTimeline';
import './MenuButton.css';

const wobbly = (val) => spring(val, presets.wobbly);

export default class MenuButton extends Component {
  static propTypes = {
    onClick: PropTypes.func,
    open: PropTypes.bool
  };

  constructor(props) {
    super(props);
    this.state = {
      y: 20,
      opacity: 0,
      line1X: 20,
      line1ScaleX: 0,
      line2X: 20,
      line2ScaleX: 0,
      line3X: 20,
      line3ScaleX: 0,
      line2Right: 0,
      atPage: 1
    };

    this.timeline = [
      ['0', () => ({
        y: wobbly(0),
        opacity: spring(1),
        line1X: wobbly(0),
        line1ScaleX: wobbly(1)
      })],
      ['+300', () => ({
        line2X: wobbly(0),
        line2ScaleX: wobbly(1)
      })],
      ['+300', () => ({
        line3X: wobbly(0),
        line3ScaleX: wobbly(1)
      })]
    ];
  }

  componentDidMount() {
    setStateWithTimeline(this, this.timeline);

    const checkPage = () => {
      const atPage = Math.floor(window.scrollY / window.innerHeight + 1);
      if (atPage !== this.state.atPage) this.setState({ atPage });
    };
    checkPage();
    window.addEventListener('scroll', () => {
      checkPage();
    });
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.open) {
      this.setState({
        line2Right: wobbly(5)
      });
    } else {
      this.setState({
        line2Right: wobbly(0)
      });
    }
  }

  render() {
    const { onClick, open } = this.props;

    return (
      // <svg width="45px" height="45px" viewBox="0 0 45 45">
      //   <rect fill="none" stroke="black" x="0" y="0" width="45" height="45"></rect>
      //   <path d="M11,16 L34,16" stroke="#050505" strokeWidth="1" strokeLinecap="square" fill="none"></path>
      //   <path d="M17,22 L34,22" stroke="#050505" strokeWidth="1" strokeLinecap="square" fill="none"></path>
      //   <path d="M11,28 L34,28" stroke="#050505" strokeWidth="1" strokeLinecap="square" fill="none"></path>
      // </svg>
      <Motion style={this.state}>
        {({ y, line1X, line2X, line3X, opacity, line1ScaleX, line2ScaleX, line3ScaleX, line2Right }) =>
          <div onClick={onClick} className={c("menuButton", { 'open': open }, `atPage${this.state.atPage}`)} style={{
            opacity,
            transform: `translateY(${y}px)`
          }}>
            <span style={{ transform: `translateX(${line1X}px) scaleX(${line1ScaleX})` }}> </span>
            <span style={{ marginRight: `${line2Right}px`, transform: `translateX(${line2X}px) scaleX(${line2ScaleX})` }}> </span>
            <span style={{ transform: `translateX(${line3X}px) scaleX(${line3ScaleX})` }}> </span>
          </div>
        }
      </Motion>
    );
  }
}



