import React, { Component, PropTypes } from 'react';
import { Motion, spring, presets } from 'react-motion';
import setStateWithTimeline from '../../helpers/setStateWithTimeline';
import c from 'classnames';
import './Menu.css';

const animate = (val) => spring(val, presets.gentle);

export default class Menu extends Component {
  static propTypes = {
    open: PropTypes.bool.isRequired
  };

  constructor(props) {
    super(props);
    this.state = {
      col1Height: 0,
      col2Height: 0,
      h1_1opacity: 0,
      h1_1y: 20,
      h1_2opacity: 0,
      h1_2y: 20,
      h1_3opacity: 0,
      h1_3y: 20
    };

  }

  componentDidMount() {
    if (this.props.open) {
      const timeline = [
        ['0', () => ({ col1Height: animate(100) })],
        ['+200', () => ({ col2Height: animate(100) })],
        ['+200', () => ({
          h1_1opacity: spring(1),
          h1_1y: animate(0)
        })],
        ['+100', () => ({
          h1_2opacity: spring(1),
          h1_2y: animate(0)
        })],
        ['+100', () => ({
          h1_3opacity: spring(1),
          h1_3y: animate(0)
        })]
      ];
      if (this.timeouts) this.timeouts.forEach(clearTimeout);
      this.timeouts = setStateWithTimeline(this, timeline);
    }
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.open) {
      const timeline = {
        '0': () => ({ col1Height: animate(100) }),
        '200': () => ({ col2Height: animate(100) }),
        '300': () => ({
          h1_1opacity: spring(1),
          h1_1y: animate(0)
        }),
        '400': () => ({
          h1_2opacity: spring(1),
          h1_2y: animate(0)
        }),
        '500': () => ({
          h1_3opacity: spring(1),
          h1_3y: animate(0)
        })
      }
      if (this.timeouts) this.timeouts.forEach(clearTimeout);
      this.timeouts = setStateWithTimeline(this, timeline);
    } else {
      const timeline = {
        '0': () => ({
          h1_1opacity: spring(0),
          h1_1y: animate(20)
        }),
        '100': () => ({
          h1_2opacity: spring(0),
          h1_2y: animate(20)
        }),
        '200': () => ({
          h1_3opacity: spring(0),
          h1_3y: animate(20)
        }),
        '300': () => ({ col2Height: animate(0) }),
        '500': () => ({ col1Height: animate(0) })
      };
      if (this.timeouts) this.timeouts.forEach(clearTimeout);
      this.timeouts = setStateWithTimeline(this, timeline);
    }
  }

  render() {
    return (
      <Motion style={this.state}>
        {({ col1Height, col2Height, h1_1opacity, h1_1y, h1_2opacity, h1_2y, h1_3opacity, h1_3y }) =>
          <div className={c('menu')} style={{ height: `${Math.max(col1Height, col2Height)}vh` }}>
            <div className="menu__col1" style={{
              clipPath: `polygon(0% 0%, 51% 0%, 51% 100%, 0% 100%)`,
              WebkitClipPath: `polygon(0% 0%, 51% 0%, 51% 100%, 0% 100%)`,
              height: `${Math.abs(col1Height)}vh`
            }}></div>
            <div className="menu__col2" style={{
              clipPath: `polygon(50% 0%, 100% 0%, 100% 100%, 50% 100%)`,
              WebkitClipPath: `polygon(50% 0%, 100% 0%, 100% 100%, 50% 100%)`,
              height: `${Math.abs(col2Height)}vh`
            }}></div>
            <div className="menu__body">
              <h1 style={{
                transform: `translateY(${h1_1y}px)`,
                opacity: h1_1opacity
              }}>WORK</h1><br/>
              <h1 style={{
                transform: `translateY(${h1_2y}px)`,
                opacity: h1_2opacity
              }}>PUBLISHED PROJECTS</h1><br/>
              <h1 style={{
                transform: `translateY(${h1_3y}px)`,
                opacity: h1_3opacity
              }}>AWARDS</h1>
            </div>
          </div>
        }
      </Motion>
    );
  }
}



